.step-selector{
    padding-top: 2rem;
    border-radius: 1rem;
    box-shadow: var(--color-layer-3, gray) 0 0 1rem;
    background-color: var(--color-layer-2, lightgray);
    width: 90vw;
    min-height: 70vh;
    display: flex;
    overflow: hidden scroll;
}

.step-selector-confirm .w3-modal-content{
    padding: 1rem;
    border-radius: 1rem;
}

.step-selector-confirm input[type='text']{
    width: 100%;
}

.children>div.tree-node>h3{
    font-size: 1rem;
}

div.tree-node.selected>h3{
    background-color: var(--color-layer-2,lightgray);
}

.tree-node>.children{
    padding-left: 1rem;
}

.tree{
    flex: 1;
    background-color: var(--color-layer-1, whitesmoke);
}

.tree>.tree-node h3{
    padding-left: 0.5rem;
}

.data{
    max-height: 70vh;
    overflow: hidden scroll;
    flex: 3;
    background-color: white;
}

.data tr, .tree-node{
    cursor: pointer;
}

.data tr:hover{
    background-color: var(--color-layer-2, lightgray);
}