.step-view{
    margin: .5rem 0;
    background-color: var(--color-layer-1, whitesmoke);
    padding: 1.5rem 2rem;
    border-radius: 1rem;
    overflow: hidden;
}

.step-view span:nth-child(3){
    word-break: break-all;
}

.step-view span:last-of-type{
    text-align: end;
    word-wrap: none!important;
    display: inline-block;
}

input {
    background-color: white;
    border: none;
    padding: .2rem .5rem;
    margin: 0 1px;
    box-sizing: border-box;
}

input[type=button]:hover{
    background-color: var(--color-layer-2, lightgray);
}

input[type=text]{
    width: max-content;
}