.schedule{
    padding-bottom: 4rem;
}

.steps{
    max-height: 80vh;
    overflow-y: scroll;
    width: 100%;
    display: block;
}

.conversion-selector select{
    margin: 10px 0;
    height: 3rem;
    border-color: var(--color-layer-2, lightgray);
    border-radius: 1em;
    background-color: var(--color-layer-2, lightgray);
    padding: 0.75rem!important;
    text-align: center;
}

.button{
    margin: 10px 0;
    height: 3rem;
    border-color: var(--color-layer-2, lightgray);
    border-radius: 1em;
    background-color: var(--color-layer-2, lightgray);
    padding: 0.75rem!important;
    text-align: center;
}

.button:active{
    background-color: var(--color-layer-1, whitesmoke);
}

.button:hover{
    background-color: var(--color-layer-3, gray);
}

.sum{
    padding: 1rem;
    border-top: 1px solid var(--color-layer-3, gray);
}