.schedule-list{
  background-color: var(--color-layer-1, whitesmoke);
  overflow-y: scroll;
  height: 100vh;
  z-index: 1;
  transition: 0.5s linear;
}

@media (max-width: 601px){
  
  .hidden{
    margin-left: calc(100% * -1);
  }
  .schedule-list{
    position: fixed;
    width: 40vw;
    box-sizing: border-box;
    box-shadow: 0 4px 4px 4px lightgray;
  }
}

.list-item{
  padding: .5rem;
  cursor: pointer;
}

.list-item:hover{
  background-color: var(--color-layer-3, gray);
}

.icon{
  display: inline-block;
  width:  16px;
  height: 16px;
  background-repeat: no-repeat;
  margin: .25em;
}

.delete{
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M3 6l3 18h12l3-18h-18zm19-4v2h-20v-2h5.711c.9 0 1.631-1.099 1.631-2h5.316c0 .901.73 2 1.631 2h5.711z"/></svg>');
}

.edit{
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24"><path d="M7.127 22.564l-7.126 1.436 1.438-7.125 5.688 5.689zm-4.274-7.104l5.688 5.689 15.46-15.46-5.689-5.689-15.459 15.46z"/></svg>');
}