.segment-manager{
  padding-top: 4rem;
  border-radius: 1rem;
  box-shadow: var(--color-layer-3, gray) 0 0 1rem;
  background-color: var(--color-layer-2, lightgray);
  width: 90vw;
  overflow: hidden scroll;
  max-height: 70vh;
}

thead>tr{
  position: sticky;
  top: 0;
}

table{
  word-wrap: break-word;
  word-break: break-all;
  max-width: 100%;
}

.segment-form{
  background-color: white;
  display: block;
}

input[type=text]{
  margin: 1px;
}

.close{
  padding-right: .5rem;
}

h3{
  padding-left: .5rem;
}